import Tables from '_c/tables';
import Edit from './user-edit.vue';
import Add from './user-add.vue';
import userApi from '@/api/user-api';
import roleApi from '@/api/role-api';
export default {
  components: {
    Tables: Tables,
    Edit: Edit,
    Add: Add
  },
  data: function data() {
    var _this = this;

    return {
      editModal: {
        show: false
      },
      addModal: {
        show: false
      },
      user: {
        roleSelectedList: [],
        roleList: []
      },
      columns2: [{
        title: '姓名',
        key: 'userName' //  width: 100,
        //  fixed: 'left'

      }, {
        title: '电话',
        key: 'mobile' //  width: 100

      }, {
        title: '密码',
        key: 'password' //  width: 100

      }, {
        title: 'Action',
        key: 'action',
        //  fixed: 'right',
        //  width: 120,
        render: function render(h, params) {
          return h('div', [h('Button', {
            props: {
              type: 'success',
              size: 'small'
            },
            on: {
              'click': function click(e) {
                userApi.getUserById({
                  id: _this.userList[params.index].id
                }, function (data) {
                  data.result.user.roleSelectedList = [];
                  data.result.user.roleList = [];
                  _this.user = data.result.user;
                  _this.user.roleSelectedList = data.result.userRoleDTOList;
                  var tempSelectedRoleList = data.result.userRoleDTOList;
                  roleApi.getRolesByType({
                    type: 0
                  }, function (data2) {
                    console.log(data2);
                    var tempList = [];

                    for (var i = 0; i < data2.result.length; i++) {
                      var count = 0;

                      for (var j = 0; j < tempSelectedRoleList.length; j++) {
                        if (data2.result[i].roleName != tempSelectedRoleList[j].roleName) {
                          count++;
                        }
                      }

                      if (count == tempSelectedRoleList.length) {
                        tempList.push(data2.result[i]);
                      }
                    }

                    _this.user.roleList = tempList;
                    console.log(_this.user);
                    _this.editModal.show = true;
                  });
                });
              }
            },
            style: {
              marginRight: '5px'
            }
          }, '修改'), h('Poptip', {
            props: {
              confirm: true,
              title: '你确定要删除吗?'
            },
            on: {
              'on-ok': function onOk() {
                console.warn(params.index);
                userApi.deleteUser({
                  id: _this.userList[params.index].id
                }, function (data) {
                  _this.userList.splice(params.index, 1);

                  _this.total = _this.total - 1;

                  _this.$Message.success("删除成功！");
                });
              }
            }
          }, [h('Button', {
            props: {
              type: 'error',
              size: 'small'
            }
          }, '删除')])]);
        }
      }],
      userList: [],
      pageIndex: 1,
      pageSize: 10,
      total: 0
    };
  },
  methods: {
    addModalClose: function addModalClose() {
      this.addModal.show = false;
      this.getUsers();
    },
    editModalClose: function editModalClose() {
      this.editModal.show = false;
      this.getUsers();
    },
    getUsers: function getUsers() {
      var _this2 = this;

      userApi.getUsers({
        pageIndex: this.pageSize * (this.pageIndex - 1) + 1,
        pageSize: this.pageSize
      }, function (data) {
        console.log(data);
        _this2.userList = data.result.list;
        _this2.total = data.result.total;
      });
    },
    pageChange: function pageChange(pageIndex) {
      console.log(pageIndex);
      this.pageIndex = pageIndex;
      this.getUsers();
    },
    pageSizeChange: function pageSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = parseInt(pageSize);
      this.getUsers();
    }
  },
  mounted: function mounted() {
    this.getUsers();
  }
};